import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
import page from '@/components/common-components/page.vue';
import pageView from '@/components/common-ui/page-view.vue';
import { deepClone } from '@/until/index';
import payoffForm from './payoffForm.vue';
let priceConfig = class priceConfig extends Vue {
    constructor() {
        super(...arguments);
        this.className = '发薪方案';
        this.validity = {};
        this.valid = undefined;
        this.model = {
            '0': {
                per: 0,
                money: 0
            },
            '1': {
                per: 0,
                money: 0
            }
        };
        this.schema = {
            groups: [
                {
                    fields: [
                        {
                            component: payoffForm,
                            modelKey: '0',
                            label: '帮我取薪资',
                            rules: {
                                required: true,
                                require: (val) => {
                                    return typeof val.per === 'number' && typeof val.money === 'number';
                                }
                            },
                            messages: {
                                require: '方案数字必填'
                            }
                        },
                        {
                            component: payoffForm,
                            modelKey: '1',
                            label: '帮我做薪资',
                            rules: {
                                required: true,
                                require: (val) => {
                                    return typeof val.per === 'number' && typeof val.money === 'number';
                                }
                            },
                            messages: {
                                require: '方案数字必填'
                            }
                        }
                    ]
                },
                {
                    fields: [
                        {
                            type: 'submit',
                            label: '保存'
                        }
                    ]
                }
            ]
        };
        this.options = {
            scrollToInvalidField: true,
            layout: 'standard' // standard fresh
        };
    }
    get schoolInfo() {
        return this.$store.state.schoolInfo;
    }
    submitHandler(e) {
        e.preventDefault();
        console.log(this.model, 'ppppppppppppppp');
        this.saveSchoolInfo('payoffConfig', this.model);
    }
    validateHandler(result) {
        this.validity = result.validity;
        this.valid = result.valid;
        console.log('validity', result.validity, result.valid, result.dirty, result.firstInvalidFieldIndex);
    }
    reset(e) {
        this.$refs.addForm.reset();
    }
    saveSchoolInfo(type, value) {
        this.$store.dispatch('updateSchoolInfo', {
            schoolId: this.schoolInfo.id,
            type: type,
            value: value
        }).then((e) => {
            if (e.success) {
                this.showToastType();
            }
            this.$store.commit('SET_SCHOOL_INFO_BY_TYPE', {
                type: type,
                value: value
            });
        });
    }
    showToastType() {
        const toast = this.$createToast({
            txt: '保存成功',
            type: 'correct'
        });
        toast.show();
    }
    initModelData() {
        let basicfeeConfig = deepClone(this.schoolInfo.payoffConfig || {
            '0': {
                per: 0,
                money: 0
            },
            '1': {
                per: 0,
                money: 0
            }
        });
    }
    mounted() {
        this.initModelData();
    }
};
priceConfig = __decorate([
    Component({
        components: {
            page,
            pageView
        }
    })
], priceConfig);
export default priceConfig;
